.yesContainer {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    opacity: 0;
    animation: 1s ease-out 0s 1 forwards appear;
}

.message {
    color: white;
    font-family: 'Montserrat Alternates', sans-serif;
    font-size: 1rem;
    text-align: center;
}

.firstMessage {
    margin-top: 1rem;
}

@keyframes appear {
    from {
        opacity: 0;
    }
    to {
        opacity: 1;
    }
}