.modalContainer {
    background-color: rgba(0, 0, 0, 0.5);
    z-index: 99;
    width: 100%;
    height: 100%;
    position: absolute;
    display: flex;
    align-items: center;
    justify-content: center;
}

.modalBox {
    width: 200px;
    height: 100px;
    background-color: rgba(255, 255, 255);
    border-radius: 0.25rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    row-gap: 1rem;
    padding: 1rem;
}

.modalBox span {
    font-family: 'Montserrat Alternates', sans-serif;
    font-size: 1rem;
    text-align: center;
}